'use client'

import { ImageUpload } from '@/components/ui/image-upload'
import { useState } from 'react'
import Image from 'next/image'
import { Copy, Check } from 'lucide-react'
import { Button } from '@/components/ui/button'

export default function TestUpload() {
  const [images, setImages] = useState<string[]>([])
  const [copiedUrl, setCopiedUrl] = useState<string | null>(null)

  const handleCopyUrl = async (url: string) => {
    try {
      await navigator.clipboard.writeText(url)
      setCopiedUrl(url)
      setTimeout(() => setCopiedUrl(null), 2000) // Reset after 2 seconds
    } catch (err) {
      console.error('Failed to copy URL:', err)
    }
  }

  return (
    <div className="container mx-auto p-8">
      <h1 className="text-2xl font-bold mb-4">Test Image Upload</h1>
      
      <div className="max-w-3xl space-y-8">
        <ImageUpload
          value={images}
          onChange={setImages}
          onRemove={(url) => setImages(images.filter(image => image !== url))}
        />

        {images.length > 0 && (
          <div className="space-y-6">
            <h2 className="text-xl font-semibold">Uploaded Images</h2>
            <div className="space-y-4">
              {images.map((url, index) => (
                <div 
                  key={url} 
                  className="bg-white rounded-lg border p-4 space-y-4"
                >
                  <div className="flex items-center justify-between gap-4">
                    <h3 className="font-medium">Image {index + 1}</h3>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => handleCopyUrl(url)}
                      className="flex items-center gap-2"
                    >
                      {copiedUrl === url ? (
                        <>
                          <Check className="h-4 w-4" />
                          Copied!
                        </>
                      ) : (
                        <>
                          <Copy className="h-4 w-4" />
                          Copy URL
                        </>
                      )}
                    </Button>
                  </div>
                  
                  <div className="relative aspect-video">
                    <Image
                      src={url}
                      alt={`Uploaded image ${index + 1}`}
                      fill
                      className="object-contain rounded-md"
                    />
                  </div>

                  <div className="bg-gray-50 p-3 rounded-md">
                    <p className="text-sm font-mono text-gray-600 break-all">
                      {url}
                    </p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}
      </div>
    </div>
  )
} 